.container_2 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  height: 1300px;
  z-index: 50;
  position: relative;
  overflow: hidden;
  background-color: #fff;
}
.container_2 .main {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 85%;
  background-color: #161933;
}
.container_2 .main .top_content {
  display: flex;
  justify-content: center;
}
.container_2 .main .top_content .text_box h1 {
  color: #fff;
  font-size: 80px;
  margin-bottom: 10px;
  letter-spacing: 2px;
  font-family: "Cafe24Dangdanghae";
}
@font-face {
  font-family: "Cafe24Dangdanghae";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.2/Cafe24Dangdanghae.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_2 .main .top_content .text_box h1 span {
  color: #ff5100;
}
.container_2 .main .top_content .text_box p {
  font-size: 18px;
  color: #e3e4f0;
  margin-bottom: 10px;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_2 .main .top_content .text_box h2 {
  margin-top: 30px;
  width: 230px;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
  text-align: center;
  border: 1px solid gray;
  position: relative;
}
.container_2 .main .top_content .text_box h2 a {
  display: block;
  width: 100%;
  color: #fff;
  font-size: 15px;
  height: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 12;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_2 .main .top_content .text_box h2 .bg {
  position: absolute;
  background-color: transparent;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  transition: all 0.5s;
  z-index: 11;
}
.container_2 .main .top_content .text_box h2:hover a {
  color: #161933;
  font-weight: bold;
}
.container_2 .main .top_content .text_box h2:hover .bg {
  left: 0;
  background-color: #fff;
  width: 100%;
}
.container_2 .main .bottom_content {
  width: 50%;
  height: 50%;
  display: flex;
  justify-content: center;
}
.container_2 .main .bottom_content .list {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
}
.container_2 .main .bottom_content .list .boxs {
  border-radius: 15px;
  margin-right: 20px;
  width: 380px;
  height: 500px;
  background-color: rgb(225, 224, 230);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.container_2 .main .bottom_content .list .boxs .bot_textbox {
  width: 100%;
  height: 20%;
  padding-left: 30px;
}
.container_2 .main .bottom_content .list .boxs .bot_textbox h1 {
  font-size: 20px;
  margin-bottom: 5px;
  letter-spacing: 2px;
  font-family: "GongGothicMedium";
  color: #161933;
}
@font-face {
  font-family: "GongGothicMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-10@1.0/GongGothicMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_2 .main .bottom_content .list .boxs .bot_textbox p {
  font-size: 14px;
  margin-bottom: 5px;
  font-family: "NEXON Lv1 Gothic OTF";
  color: #4c4d5e;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_2 .main .bottom_content .list .boxs .bot_imgbox {
  width: 85%;
  height: 250px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #fff;
}
.container_2 .main .bottom_content .list .boxs .bot_imgbox img {
  width: 70%;
}
.container_2 .main .bottom_content .list .boxs .bot_imgbox h2 {
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  color: #4e5174;
  font-size: 13px;
  border-bottom: 1px solid rgb(198, 202, 224);
  font-weight: bold;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
