  font-style: normal;
}
.container_2 .main .bottom_content .list .boxs .bot_imgbox .bot_imgbox_list {
  display: flex;
  justify-content: center;
}
.container_2 .main .bottom_content .list .boxs .bot_imgbox .bot_imgbox_list li {
  font-size: 12px;
  margin: 0 5px;
  width: 100%;
  background-color: #808199;
  color: #fff;
  border-radius: 20px;
  padding: 5px;
}

