.container_1 {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: "Cafe24Dangdanghae";
  overflow: hidden;
  z-index: 50;
  position: relative;
  background-color: #fff;
}
@font-face {
  font-family: "Cafe24Dangdanghae";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.2/Cafe24Dangdanghae.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_1 .section1 {
  display: flex;
  width: 1200px;
  height: 100vh;
  align-items: center;
}
.container_1 .section1 .leftsec {
  width: 600px;
}
.container_1 .section1 .leftsec h1 {
  font-size: 55px;
  letter-spacing: 2px;
  margin-bottom: 5px;
  color: #242e55;
}
.container_1 .section1 .leftsec h4 {
  letter-spacing: 1px;
  margin-bottom: 5px;
  font-size: 18px;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_1 .section1 .leftsec p {
  margin-top: 30px;
  position: relative;
  transition: all 0.3s;
}
.container_1 .section1 .leftsec p a {
  transition: all 0.3s;
  position: absolute;
  left: 0;
  display: block;
  width: 150px;
  height: 40px;
  font-size: 16px;
  background-color: #ff5100;
  color: #fff;
  line-height: 40px;
  text-align: center;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_1 .section1 .leftsec p:hover a {
  width: 160px;
}
.container_1 .section1 .rigthsec {
  width: 700px;
}
.container_1 .section1 .rigthsec .list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 600px;
  position: relative;
}
.container_1 .section1 .rigthsec .list .box {
  width: 45%;
  height: 50%;
  margin-right: 20px;
  border-radius: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  position: relative;
  background-color: rgb(247, 247, 247);
}
.container_1 .section1 .rigthsec .list .box .center_img {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container_1 .section1 .rigthsec .list .box .center_img img {
  max-width: 70%;
  height: 50%;
}
.container_1 .section1 .rigthsec .list .box p {
  position: absolute;
  top: 25%;
  left: 20%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 10px;
  color: #fff;
  font-size: 13px;
  letter-spacing: 1px;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container_1 .section1 .rigthsec .list .box:nth-child(1) p {
  background-color: #ff5100;
  width: 100px;
}
.container_1 .section1 .rigthsec .list .box:nth-child(2) p {
  background-color: #182041;
  width: 100px;
}
.container_1 .section1 .rigthsec .list .box:nth-child(3) p {
  background-color: #182041;
  width: 150px;
}
.container_1 .section1 .rigthsec .list .box:nth-child(4) p {
  background-color: #ff5100;
  width: 100px;
}
.container_1 .section1 .rigthsec .list .box:nth-child(1) img {
  width: 100%;
}
.container_1 .section1 .rigthsec .list .center_box {
  position: absolute;
  top: 50%;
  left: 48.5%;
  transform: translate(-50%, -50%);
  width: 130px;
  height: 130px;
  border-radius: 50%;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 7px solid #ff5100;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container_1 .section1 .bg_animation {
  position: relative;
}
.container_1 .section1 .bg_animation > div {
  position: absolute;
  border-radius: 50%;
}
.container_1 .section1 .bg_animation .animation_1 {
  width: 350px;
  height: 350px;
  background-color: #ff5100;
  right: -330px;
  bottom: -530px;
  animation: fadeInUp 2s infinite alternate;
}
.container_1 .section1 .bg_animation .animation_2 {
  width: 250px;
  height: 250px;
  background-color: rgba(2, 7, 21, 0.95);
  bottom: -620px;
  right: -200px;
  animation: fadeInUp5 1.5s infinite alternate;
}
.container_1 .section1 .bg_animation .animation_3 {
  width: 350px;
  height: 350px;
  background-color: rgba(2, 7, 21, 0.95);
  right: 1170px;
  bottom: -530px;
  animation: fadeInUp 2s infinite alternate;
}
.container_1 .section1 .bg_animation .animation_4 {
  width: 250px;
  height: 250px;
  background-color: #ff5100;
  bottom: -620px;
  right: 1100px;
  animation: fadeInUp5 1.5s infinite alternate;
}

