.container3 {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 50;
  position: relative;
  background-color: #fff;
}
.container3 .main {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 85%;
  align-items: center;
  background-color: #fff;
  overflow: hidden;
}
.container3 .main .top_content3 {
  width: 30%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.container3 .main .top_content3 h1 {
  letter-spacing: 2px;
  font-size: 50px;
  color: #161933;
  margin-bottom: 10px;
  font-family: "Cafe24Dangdanghae";
}
@font-face {
  font-family: "Cafe24Dangdanghae";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.2/Cafe24Dangdanghae.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container3 .main .top_content3 h1 span {
  color: #ff5100;
}
.container3 .main .top_content3 .top_textbox {
  margin-top: 30px;
}
.container3 .main .top_content3 .top_textbox p {
  font-size: 16px;
  color: #161933;
  margin-bottom: 10px;
  font-family: "NEXON Lv1 Gothic OTF";
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container3 .main .bot_content3 {
  width: 50%;
  display: flex;
  justify-content: center;
}
.container3 .main .bot_content3 .con3_list {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-left: 40px;
}
.container3 .main .bot_content3 .con3_list .boxs {
  border-radius: 15px;
  margin: 0 10px;
  width: 360px;
  height: 400px;
  background-color: #f3f3f3;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
}
.container3 .main .bot_content3 .con3_list .boxs .cont3_imgbox {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.container3 .main .bot_content3 .con3_list .boxs .cont3_imgbox img {
  width: 100%;
  height: 100%;
}
.container3 .main .bot_content3 .con3_list .boxs .cont3_textbox {
  width: 100%;
  height: 20%;
  padding-left: 30px;
}
.container3 .main .bot_content3 .con3_list .boxs .cont3_textbox h1 {
  font-size: 20px;
  margin-bottom: 15px;
  letter-spacing: 2px;
  font-family: "GongGothicMedium";
  color: #161933;
}
@font-face {
  font-family: "GongGothicMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-10@1.0/GongGothicMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.container3 .main .bot_content3 .con3_list .boxs .cont3_textbox p {
  font-size: 14px;
  margin-bottom: 5px;
  font-family: "NEXON Lv1 Gothic OTF";
  color: #4c4d5e;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
